/* eslint-disable */
<template>
    <div id="app" class="raiz">
        <div class="vis-header">
            <h4>¿Cuánto invierten en infraestructura los países de América Latina y el Caribe?</h4>
            <p>Suma de la inversión pública y privada en infraestructura como porcentaje del PIB.</p>
        </div>

        <div class="field is-horizontal vis-controls">
            <div class="field-label is-normal">
                <label class="label">País: </label>
            </div>
            <div class="field-body">
                <div class="field">
                    <div class="control">
                        <div class="select is-info">
                            <select id="country-select" class="is-hovered" @change="loadCountryData" v-model="selectedCountry">
                                <option v-for="country in countries" v-bind:value="country">{{country}}</option>
                            </select>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="vis-body">
            <div class="vis-content">
                <vue-plotly :data="vis1.data" :layout="vis1.layout" :options="vis1.options"/>
            </div>
        </div>

        <div class="vis-footer">
            <p>
                <small>Fuente: INFRALATAM, BID (2019)</small>
            </p>
        </div>
    </div>
</template>

<script>

    import * as d3 from "d3";
    import VuePlotly from '@statnett/vue-plotly';

    import Plotly from 'plotly.js'
    import plotES from 'plotly.js/lib/locales/es'

    Plotly.register(plotES);
    Plotly.setPlotConfig({ locale: 'es' });

    export default {
        name: 'app',
        components: {
            VuePlotly
        },
        data: () => {
            return {
                vis1: {
                    data: [],
                    layout: {
                        //title: 'Inversión como porcentaje del PIB',
                        margin: {
                            l: 50,
                            r: 20,
                            b: 30,
                            t: 30,
                            pad: 0
                        },
                        font: {
                            family: 'Open Sans, Helvetica, Arial, sans-serif',
                            size: 12,
                            color: '#3C3B3B'
                        },
                        xaxis: {
                            fixedrange: true
                        },
                        yaxis: {
                            title: '% PIB',
                            fixedrange: true,
                            range: [0, 2],
                            hoverformat: '.2f'
                        },
                        colorway: ['#1D6996', '#0F8554', '#CC503E', '#222', '#EDAD08'],
                        legend: {
                            x: 0,
                            y: -0.1,
                            orientation: "h"
                        }
                    },
                    options: {},
                },
                infralatam_data: undefined,
                countries: [],
                selectedCountry: undefined
            }
        },
        mounted: function () {
            this.loadData();
        },
        methods: {
            loadData: function () {
                let vm = this;

                d3.csv(process.env.BASE_URL + "data/infralatam_totales.csv").then(function (data) {
                    vm.infralatam_data = data;
                    vm.loadSelectOptions(data);
                });
            },
            loadSelectOptions: function (data) {
                let vm = this;
                vm.countries = [...new Set(data.map(x => x.country_es))];
                vm.selectedCountry = "Argentina";
                this.loadCountryData();
            },
            loadCountryData: function () {
                let vm = this;

                let country_data = vm.infralatam_data.filter(x => x.country_es === vm.selectedCountry);
                let maxValue = d3.max(country_data.map(x => +x.gdp_por));

                let valuesBySector = d3.nest()
                    .key(function (d) {
                        return d.sector_es;
                    })
                    .entries(country_data)
                    .sort(function (a, b) {
                        return a.year - b.year;
                    });

                vm.vis1.layout.yaxis.range = [0, maxValue * 1.02];

                vm.vis1.data = valuesBySector.map(function (d) {
                    let x = [];
                    let y = [];
                    d.values.forEach(function (d) {
                        x.push(d.year);
                        y.push(d.gdp_por);
                    });
                    return {
                        type: 'scatter',
                        x: x,
                        y: y,
                        name: d.key,
                        line: {
                            // color: "#CC503E",
                            shape: 'spline',
                            width: 3
                        }
                    }
                });
            }
        }
    }
</script>

